

.about {
  margin-top: 72px;

  @media (min-width: $tablet-width) {
    margin-top: 97px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 71px;
  }

  .title {
    padding: 0 0 67px;
    text-align: center;

    @media (min-width: $tablet-width) {
      padding: 0 0 88px;
    }

    @media (min-width: $desktop-width) {
      padding: 0 0 102px;
    }
  }
}

.about__container {

  @media (min-width: $tablet-width) {
    padding: 0 10px;
  }
  @media (min-width: $desktop-width) {
    padding-left: 110px;
    padding-right: 103px;
  }
}

.about__wrapper {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
  }
}

.about__text {

  p {
    margin: 5px 0;

    @media (min-width: $desktop-width) {
      margin: 8px 0;
    }
  }

  .partners__list {
    @include visually-hidden;
  }

  @media (min-width: $tablet-width) {
    width: 446px;
    margin-right: auto;

    .partners__list {
      position: relative;
      width: auto;
      height: auto;
      margin: 0;
      clip: unset;
      justify-content: flex-start;
      align-items: center;
    }
  }

  @media (min-width: $desktop-width) {
    width: 630px;
  }
}

.about__img {
  width: 100%;
  object-fit: cover;
  height: 200px;
  margin-top: 20px;
  margin-bottom: 19px;

  @media (min-width: $tablet-width) {
    height: 255px;
    margin-top: 14px;
  }

  @media (min-width: $desktop-width) {
    height: 360px;
    margin-top: 25px;
  }
}

.about__social {
  margin-top: 31px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
    flex-direction: column;
  }
}

.about__partners {
  margin-top: 10px;

  @media (min-width: $tablet-width) {
    margin-top: 29px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 18px;
  }
}
