.advantages {

  background-image: url("../img/bg-advantages-mobile.svg");
  background-repeat: no-repeat;
  //background-size: cover;
  background-position-x: center;
  margin-top: -179px;
  background-size: 100% 407px;

  @media (min-width: $tablet-width) {
    background-image: url("../img/bg-advantages-tablet.svg");
    background-size: 100% 521px;
    margin-top: -152px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/bg-advantages-desktop.svg");
    background-size: 1080px 678px;
    margin-top: -201px;
  }


  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  //margin: 0 auto;

}

.advantages__container {
  height: 407px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media (min-width: $tablet-width) {
    height: 521px;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    height: 678px;
    padding: 0 20px;
  }
}

.advantages__item {
  flex-direction: column;
}

.advantages__desc {
  font-size: 20px;
  line-height: 30px;
  font-weight: 700;
  text-transform: uppercase;
  max-width: 214px;
  display: inline-block;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    max-width: 200px;
  }

  @media (min-width: $desktop-width) {
    font-size: 24px;
  }
}

.advantages__icon {
  margin-bottom: 26px;

  @media (min-width: $tablet-width) {
    margin-bottom: 36px;
  }
}

.advantages__icon--container {
  width: 90px;
  height: 57px;

  @media (min-width: $desktop-width) {
    width: 104px;
    height: 66px;
  }
}

.advantages__icon--shipment {
  width: 60px;
  height: 60px;

  @media (min-width: $desktop-width) {
    width: 70px;
    height: 70px;
  }
}

.advantages__icon--automobile {
  width: 104px;
  height: 44px;

  @media (min-width: $desktop-width) {
    width: 121px;
    height: 51px;
  }
}


.advantages__note {
  font-size: 16px;
  line-height: 34px;
  font-weight: 400;
  display: block;
  text-transform: none;

  @media (min-width: $desktop-width) {
    font-size: 20px;
  }
}





.advantages__item {
  color: $color-primary;
  text-align: center;
}


.advantages__slider {
  padding-top: 46px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }
}



