.calc {
  background-color: #EC8931;
  padding: 20px 26px;
  margin-top: 49px;
  margin-bottom: 56px;

  @media (min-width: $tablet-width) {
    padding: 20px;
    margin-bottom: 40px;
    margin-top: 64px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 0;
    width: 210px;
  }
}

.calc__header {
  font-size: 16px;
  line-height: 20px;
  font-weight: bold;
  color: $color-primary;
}

.calc__list {
  @include ul-reset;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;

    .calc__group:first-child {
      width: 170px;
    }

    .calc__group:nth-child(2) {
      width: 240px;
    }

    .calc__group:last-child {
      width: 170px;
    }
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;

    .calc__group:first-child {
      width: 100%;
    }

    .calc__group:nth-child(2) {
      width: 100%;
    }

    .calc__group:last-child {
      width: 100%;
    }
  }
}

.calc__group {
  position: relative;
}

.calc__link {
  color: $color-primary;
}

.calc__item {
  margin-top: 20px;

  &--checkbox {
    display: flex;

    @media (min-width: $tablet-width) {
      position: absolute;
      top: -30px;
    }

    @media (min-width: $desktop-width) {
      position: relative;
      top: 0;
    }
  }
}







.calc__label {
  @include visually-hidden;

  &--checkbox {
    position: relative;
    width: 162px;
    height: auto;
    margin: 0;
    border: 0;
    padding: 0;
    clip: unset;
    overflow: hidden;
    font-size: 12px;
    line-height: 12px;
    color: $color-primary;

    @media (min-width: $tablet-width) {
      font-size: 10px;
      line-height: 10px;
      width: 122px;
    }

  }
}

.calc__input {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  height: 35px;
  border: 1px solid #c16818;
  padding: 7px 13px;
  color: #C4C4C4;

  &::placeholder {
    color: #C4C4C4;
    font-size: 12px;
    line-height: 24px;
  }

  &--checkbox {
    width: 30px;
  }
}

.calc__button {
  width: 100%;
  background-color: transparent;
  padding-top: 2px;
  padding-bottom: 2px;

  @media (min-width: $tablet-width) {
    margin-top: 4px;
  }
}

.calc__button-text {
  font-size: 12px;
  line-height: 24px;
  color: $color-primary;
}



.select-hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px;
}

.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 12px;
  line-height: 24px;
  color: $color-secondary;
  background-color: $background-color-fourth;
  width: 100%;
  height: 35px;
  border: 1px solid #C16818;
}
.select-styled {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 7px 13px;
  transition: all 0.2s ease-in;
}
.select-styled::after {
  content: "";
  width: 9px;
  height: 9px;
  background-image: url("../img/icon-arrow.svg");
  position: absolute;
  top: 13px;
  right: 13px;
}
.select-styled:active::after,
.select-styled.active::after {
  top: 13px;
  background-image: url("../img/icon-arrow.svg");
  transform: rotate(180deg);
}
.select-options {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #fff;
}
.select-options li {
  margin: 0;
  padding: 10px 0;
  text-indent: 15px;
  border-top: 1px solid #C4C4C4;
  -webkit-transition: all 0.15s ease-in;
  -o-transition: all 0.15s ease-in;
  transition: all 0.15s ease-in;
  border-right: 1px solid #C4C4C4;
  border-left: 1px solid #C4C4C4;
}
.select-options li:last-child {
  border-bottom: 1px solid #C4C4C4;
}
.select-options li:hover {
  color: #000;
  background: #fff;
}
.select-options li[rel="hide"] {
  display: none;
}
